body, h1, h2, h3, h4, h5, h6{
    color:#000;
}

.wrapper {
    width: 70%;
    margin: 0 auto;
    margin-top: 50px;
}

.wrapper h2 {
    text-align: left;
    font-size: 20px;
}

.name {
    margin: 10px 0;
}

.line {
    height: 25px;
    min-width: 90%;
    margin: 0 auto 10px;
    line-height: 25px;
    font-size: 14px;
    color:#fff;
    padding: 0 0 0 10px;
    position: relative;
}

.line:before {
    content: ' ';
    width: 100%;
    position: absolute;
    left: 0;
    height: 25px;
    top: 0;
    z-index: -2;
    background: #dcdcdc;
}

.line:after {
    content: ' ';
    background: #008fff;
    height: 25px;
    transition: 0.8s;
    display: block;
    width: 100%;
    animation: animate 1 4s;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.line1:after {
    max-width: 90%
}

.line2:after {
    max-width: 80%
}

.line3:after {
    max-width: 70%
}

.line4:after {
    max-width: 70%
}

.line5:after {
    max-width: 75%
}

.line6:after {
    max-width: 85%
}

.line7:after {
    max-width: 70%
}

@keyframes animate {
    0%{
        width: 0;
    }

    100%{
        width: 100;
    }
}

section .container{
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 10px;
    width: 100%;
    box-sizing: border-box;
}

.header {
    color:#000;
    font-size: 32pt;
    text-align: center;
    padding-top: 100px;
    font-weight: 500;
    line-height: 1.5em;
}

.bar {
    width: 600px;
}

.header-bar {
    width: 70px;
    height: 4px;
    background: #000;
    margin: 0 auto;
    margin-bottom: 75px;
}

.floatRight {
    width: 50%;
    float: right;
}

.info {
    max-width: 1200px;
    width: 100%;
    color: #000;
}

p {
    color: #000;
}

.right {
    float: right;
    width: 500px;
    text-align: center;
    font-size: 20px;
    line-height: 40px;
}

.left {
    float: left;
}

@media screen and (max-width: 1120px){
    section{
        line-height: 18pt;
        padding: 100px 0 130px 0;
    }

    .right {
        float: none;
        width: 500px;
        text-align: center;
        font-size: 20px;
        line-height: 40px;
    }
    
    .left {
        float: none;
        text-align: center;
    }
    
}

@media screen and (max-width: 375px ) {
    .right {
        float: right;
        width: 100%;
        text-align: center;
        font-size: 20px;
        line-height: 40px;
    }

    img {
        width: 100%;
    }

    .left {
        float: none;
        margin-left: 0px;
        margin: 0 auto;
    }
    
    .bar {
        width: 100%;
    }

    .wrapper {
        width: 95%;
        margin: 0 auto;
        margin-top: 50px;
    }
    
}

