.intro{
    color:#fff;
    font-size: 32pt;
    line-height: 60px;
    text-align: center;
    z-index: 1;
    width: 100%;
}

.removeDiv {
    display: none;;
}

.intro-background {
    background-color: #252934;
    height: calc(100vh + 53px);
}

section:nth-of-type(1) {
    padding: 0 10px;
    color: #fff;
}

.flex {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.text {
    min-height: 100vh;
    overflow: hidden;
    background-color: #252934;
}

.Typist {
    display: inline;
}

.Cursor--blinking {
    font-size: 32pt;
    color: #ffffff;
    -webkit-animation: 1s blink step-start infinite;
    -moz-animation: 1s blink step-start infinite;
    -ms-animation: 1s blink step-start infinite;
    -o-animation: 1s blink step-start infinite;
    animation: 1s blink step-start infinite;
}

@keyframes "blink" {
    from, to {
    color: transparent;
    }
    50% {
    color: white;
    }
}

@-moz-keyframes blink {
    from, to {
    color: transparent;
    }
    50% {
    color: white;
    }
}

@-webkit-keyframes "blink" {
    from, to {
    color: transparent;
    }
    50% {
    color: white;
    }
}

@-ms-keyframes "blink" {
    from, to {
    color: transparent;
    }
    50% {
    color: white;
    }
}

@-o-keyframes "blink" {
    from, to {
    color: transparent;
    }
    50% {
    color: white;
    }
}

/*Canvas*/

@media screen and (min-width: 600px){
    section {
        line-height: 18pt;
        padding: 100px 0 130px 0;
    }
}