.footername {
    color: #fff;
    padding-top: 10px;
}

.fab, .fas {
    color: #fff;
    font-size: 2.5em;
    margin: 0 auto;
}  

/* The Magic Float Center Code */
.float_center {
    float: right;
    position: relative;
    left: -50%; /* or right 50% */
    text-align: left;
}

.float_center > .child {
    position: relative;
    left: 50%;
}

  /* style */

.clear {
    clear:both;
}

#title {
    text-align: center;
    color: #ffffff;
    margin: 40px 0 0 0;
}

#container {
    width: 100%;
    margin: 20px auto 0 auto;
    background: #1B242F;
    padding: 30px;
}

ul {
    list-style-type: none;
    margin:0;
    padding:10px 0;
}

ul li {
    float: left;
    list-style-type: none;
    margin: 5px 6px;
}

ul a {
    display: block;
    height: 26px;
    line-height: 26px;
    text-decoration: none;
    text-transform: uppercase;
    padding: 0px 10px;
}

.copyright{
    margin: 0;
    color: #008fff;
}

.fa-spin-hover:hover {
    -webkit-animation: fa-spin 0.4s  linear;
    -moz-animation: fa-spin 0.4s  linear;
    -o-animation: fa-spin 0.4s  linear;
    animation: fa-spin 0.4s  linear;
    transition: color 0.4s ease-out;
    color: #008fff;
}